import React from 'react';
import {useState, useEffect, useRef} from 'react';
import { useLoading, useNpcName } from 'core';
import { RoadTimer, RoadArrivePlace } from 'screens';
import { useNavigate } from "react-router-dom";

interface RoadPageProps {
    aa : string;
    bb : number;
    cc : boolean;
    dd : () => void;
}

export const RoadPage = () =>{
    const {setLoading} = useLoading(); // 
    const { npcName, prevNpcName, setPrevNpcName } = useNpcName(); //  npc  
    const navigate = useNavigate(); //      
    const [arrive, setArrive]  = useState(false); //   
    const roadArea = useRef<HTMLDivElement>(null); //  useRef   .   
    const isHolding = useRef(false); //  
    const [isBoosting, setIsBoosting] = useState(false); //   

    const handleSpeedUp = () =>{
        if (!isHolding.current) {
            isHolding.current = true;
            setIsBoosting(true);
            if(roadArea.current){
                roadArea.current.classList.add('boost');
            }
        }
    }
    const handleSpeedDown = () =>{
        if (isHolding.current) {
            isHolding.current = false;
            setIsBoosting(false);
            if(roadArea.current){
                roadArea.current.classList.remove('boost');
            }
        }
    }

    useEffect(() => {
        setLoading(true);
        if(prevNpcName.length < 1) setPrevNpcName(''); //     

        //  
        const handleKeyDown = (e: KeyboardEvent) => {
            handleSpeedUp();
        };
        //  
        const handleMouseDown = (e: MouseEvent) => {
            handleSpeedUp();            
        };
        //  
        const handleKeyUp = (e: KeyboardEvent) => {
            handleSpeedDown();
        };
        //  
        const handleMouseUp = (e: MouseEvent) => {
            handleSpeedDown();
        }

        // 
        document.addEventListener("keydown", handleKeyDown);
        document.addEventListener("keyup", handleKeyUp);
        document.addEventListener("mousedown", handleMouseDown);
        document.addEventListener("mouseup", handleMouseUp);
        return () => {
            // useEffect  return       .
            //    
            document.removeEventListener("keydown", handleKeyDown);
            document.removeEventListener("keyup", handleKeyUp);
            document.removeEventListener("mousedown", handleMouseDown);
            document.removeEventListener("mouseup", handleMouseUp);
        };
    }, []);

    //    
    const getArrive = (arr : boolean | null) =>{
        if(arr === true && arr !== null){//     
            setArrive(arr);
        }
    }

    useEffect(()=>{
        if(arrive){
            setLoading(false);
            navigate("/mabinogi_peddler");
        }
    }, [arrive])

    // animation-duration  
    return(
        <div className="road-wrap" ref={roadArea}>
            <div className="player"></div>
            {/* isHolding     ? */}
            <RoadTimer speed={isBoosting ? 1 : 0} arrive={getArrive}/>
            <RoadArrivePlace />
        </div>
    )
}