const API_KEY = process.env.REACT_APP_API_KEY || 'test_7b7212e431fec3f52116b14619810636201e32488e76c577955ca1d9f1a5e3acefe8d04e6d233bd35cf2fabdeb93fb0d';
const API_URL = process.env.REACT_APP_API_URL || 'https://open.api.nexon.com/mabinogi/v1/npcshop/list';

export type ParamProps  = {
    chaNm : string;
    serNm : string;
    chnNum : number;
}

export type NpcShopProps = {
    shop_tab_count: number; // NPC   
    shop: Array<{
      tab_name: string; //   
      item: Array<{
        item_display_name: string; //  
        item_count: number; //  
        item_option: Array<{
          option_type: string; //   
          option_sub_type: string; //    
          option_value: string; //   
          option_value2: string; //    2
          option_desc: string; //    
        }>;
        price: Array<{
          price_type: string; //  
          price_value: number; //  
        }>;
        limit_type: string; //    (// )
        limit_value: number; //   
        image_url: string; //   URL
      }>;
    }>;
    date_inquire: string; //    (ISO 8601 : UTC)
    date_shop_next_update: string; //      (ISO 8601 : UTC)
  };

export const getData = async ({chaNm, serNm, chnNum}:ParamProps) => {
    const url = new URL(String(API_URL));
    url.searchParams.append('npc_name', chaNm);
    url.searchParams.append('server_name', serNm);
    url.searchParams.append('channel', String(chnNum));
    try{
        const response = await fetch(url, {
            method : 'GET',
            headers : {
                'accept' : 'application/json',
                'x-nxopen-api-key' : String(API_KEY),
            }
        });
        if (!response.ok) { // ok    > responce code 200   +> HTTP   200~299( )    ex) response.code : 200 = true / response.code : 400 = false
            throw new Error('Network response was not ok');
        }
        const data = await response.json();
        return data;

    }catch(error){
        console.error('error');
        throw error;
    }
}