import React, { useState, useEffect } from "react";
import { useNpcName } from "core";
import { NpcProps } from "type";
import { NpcData, NpcDataProps } from "datas";

export const NpcDialog = ({ buyState }: NpcProps) => {
    const { npcName } = useNpcName(); // npc  
    const [nowNpcDatas, setNowData] = useState<NpcDataProps["txtScript"] | null>(null); //   
    const [normalTxt, setNormal] = useState<string[] | null>(null); //   (  x)
    const [text, setText] = useState(""); //    (  )
    const [fullText, setFullText] = useState(""); // text     hook
    const [isUpdate, setUpdate] = useState(false); // state  
    const [shopState, setShopState] = useState(buyState ?? 'normal') // state 
    const [intervalId, setIntervalId] = useState<NodeJS.Timeout | null>(null); //   interval 

    // 15    
    const changeNormalText = () =>{
        if(normalTxt){
            const _num = Math.floor(Math.random() * normalTxt?.length);
            //     
            if(fullText === normalTxt[_num] && _num > 0){
                setFullText(normalTxt[_num - 1]);      
            }else if(fullText === normalTxt[_num] && _num === 0){
                setFullText(normalTxt[_num + 1]);
            }else{
                setFullText(normalTxt[_num]);
            }
        }        
    }

    //       
    useEffect(()=>{
        const _data = NpcData.find((el) => el.name === npcName);
        setNowData(_data?.txtScript || null);
    }, [npcName])

    //   -          
    useEffect(()=>{
        if(nowNpcDatas){
            // normal    
            const _arr = Object.keys(nowNpcDatas).filter((key)=> key.startsWith("normal"))
                        .map((key) => nowNpcDatas[key as keyof typeof nowNpcDatas]) ?? null;
            setNormal(_arr);
            setText(nowNpcDatas.visit);
        }
    }, [nowNpcDatas])

    //   
    useEffect(()=>{
        if(isUpdate) return;

        setTimeout(()=>{
            changeNormalText();
        }, 10000)

        const interval = setInterval(changeNormalText, 10000); // 10 
        setIntervalId(interval); //  interval 

        return () => clearInterval(interval); // 
    }, [normalTxt])

    //   
    useEffect(()=>{
        setShopState(buyState)            
    }, [buyState])

    //    
    useEffect(()=>{
        if(shopState === 'normal') return;
        if(nowNpcDatas){
            // buyState      
            const _findEl = nowNpcDatas[shopState as keyof typeof nowNpcDatas];
        
            // intervalId  clear state null 
            if(intervalId){
                clearInterval(intervalId);
                setIntervalId(null);
            }

            setFullText(_findEl);
        }
        
        //     normalText  
        setTimeout(() => {
            setShopState('noraml');
            changeNormalText(); //     
        }, 20000); //  20    

    }, [shopState])

    //  
    useEffect(()=>{       
        if (!fullText) return;

        let charIndex = 0;
            setText('')
            const typeEffect = () => {
                if (charIndex < fullText.length) {
                    setText((prev) => prev + fullText[charIndex - 1]);
                    charIndex++;
                    setTimeout(typeEffect, 50);
                }
            };

        typeEffect();
    }, [fullText])

    return (
        <div className="npc-dialog-wrap">
            <strong className="npc-name">{npcName}</strong>
            <div className="dialog-box">
                <p className="npc-txt">{text}</p>
            </div>
        </div>
    );
};
